
import { Request, Response } from 'express';
import { Repository, WeeklyDistribuition } from '../../types/types';
import * as HttpStatus from 'http-status-codes';
import { readMock } from '../../utils/handleMock';
import { loadAbsoluteMoment, Moment, nowLocale } from '../../utils/Moment';
import { test } from "../../utils/mannwhitneyu.js";

export async function execute(req: Request, res: Response) {
    const repository: Repository = readMock("resources/output/c/curl-curl.json")
    const weeklyContributions = normalizeDistribuition(fullFillDistribuition(repository.weekly_distribuition!, repository.created_at!))
    // const weeklyContributions = repository.weekly_distribuition!

    const splitDate = loadAbsoluteMoment(repository.newcomer_labels!![0].created_at)

    const distributionBefore = weeklyContributions.filter(distrution => loadAbsoluteMoment(distrution.week, "WW GGGG").isBefore(splitDate))
    const distributionAfter = weeklyContributions.filter(distrution => loadAbsoluteMoment(distrution.week, "WW GGGG").isAfter(splitDate))

    console.log(mann1(distributionBefore, distributionAfter))
    console.log(mann2(distributionBefore, distributionAfter))
    console.log(mann3(distributionBefore, distributionAfter))

    res.status(HttpStatus.OK).json();
}

function fullFillDistribuition(weeklyDistribuition: WeeklyDistribuition[], created_at: string) {

    const fullFillDistribution = [...weeklyDistribuition]

    for (var created = loadAbsoluteMoment(created_at); created.isBefore(nowLocale()); created.add(1, 'week')) {
        const this_week = created.format('WW GGGG')
        if (!weeklyDistribuition.some(distribution => distribution.week == this_week)) {
            fullFillDistribution.push({ week: this_week, total: 0, dates: [] })
        }
    }
    return fullFillDistribution.sort((a, b) =>
        (loadAbsoluteMoment(a.week, 'WW GGGG').valueOf() > loadAbsoluteMoment(b.week, 'WW GGGG').valueOf()) ?
            1 :
            ((loadAbsoluteMoment(b.week, 'WW GGGG').valueOf() > loadAbsoluteMoment(a.week, 'WW GGGG').valueOf())
                ? -1 : 0))

}


function normalizeDistribuition(weeklyDistribuition: WeeklyDistribuition[]) {

    const firstPR = weeklyDistribuition[0].week
    const lastPR = loadAbsoluteMoment(firstPR, "WW GGGG").add(6, 'months')
    const indexOfLast = weeklyDistribuition.map(d => d.week).findIndex(week => lastPR.isBefore(loadAbsoluteMoment(week, "WW GGGG")))

    return weeklyDistribuition.splice(indexOfLast, weeklyDistribuition.length)
}

function nearestWeek(splitDate: Moment, weekly_distribuition: WeeklyDistribuition[]) {

    let nearestWeek: string = "";

    weekly_distribuition.forEach(distribution => {

        let diff = loadAbsoluteMoment(distribution.week, "WW GGGG").diff(splitDate, 'days');
        if (diff > 0) {
            if (nearestWeek) {
                if (loadAbsoluteMoment(distribution.week, "WW GGGG").diff(loadAbsoluteMoment(nearestWeek, "WW GGGG"), 'days') < 0) {
                    nearestWeek = distribution.week;
                }
            } else {
                nearestWeek = distribution.week;
            }
        }
    });

    return nearestWeek

}

function mann1(before: WeeklyDistribuition[], after: WeeklyDistribuition[]) {
    let qtdBefore = before.map(d => d.total)
    let qtdAfter = after.map(d => d.total)

    // var qtdBefore = [2, 4, 6, 2, 3, 7, 5, 1]
    // var qtdAfter = [8, 10, 11, 14, 20, 18, 19, 9];


    let mwu = require('mann-whitney-utest');
    let samples = [qtdBefore, qtdAfter];

    //array of U values
    var u = mwu.test(samples);

    if (mwu.significant(u, samples)) {
        return 'The data is significant! [' + u + "] Correct: " + mwu.check(u, samples)
    } else {
        return 'The data is not significant. [' + u + "] Correct: " + mwu.check(u, samples)
    }

}

function mann2(before: WeeklyDistribuition[], after: WeeklyDistribuition[]) {
    var genstats = require('genstats')
    let qtdBefore = before.map(d => d.total)
    let qtdAfter = after.map(d => d.total)

    // var qtdBefore = [2, 4, 6, 2, 3, 7, 5, 1]
    // var qtdAfter = [8, 10, 11, 14, 20, 18, 19, 9];

    return genstats.wilcoxon(qtdBefore, qtdAfter)
}



function mann3(before: WeeklyDistribuition[], after: WeeklyDistribuition[]) {
    let qtdBefore = before.map(d => d.total)
    let qtdAfter = after.map(d => d.total)
    // var qtdBefore = [2, 4, 6, 2, 3, 7, 5, 1]
    // var qtdAfter = [8, 10, 11, 14, 20, 18, 19, 9];

    var t = test(qtdBefore, qtdAfter);
    // console.log(t);

    // console.log('Wilcoxon', genstats.wilcoxon(qtdBefore, qtdAfter))
    return t
}

